<template>
    <div>
      <canvas ref="chartCanvas"></canvas>
    </div>
  </template>
  
  <script>
  import { Chart as ChartJS, RadarController, PointElement, LinearScale, CategoryScale, Title, Tooltip, Legend } from 'chart.js';

  ChartJS.register(RadarController, PointElement, LinearScale, CategoryScale, Title, Tooltip, Legend);

  export default {
    name: 'RevenueChartSecond',
    data() {
      return {
        chartData: {
          labels: ['Q1', 'Q2', 'Q3', 'Q4'],
          datasets: [
            {
              label: 'Revenue',
              backgroundColor: '#ff6384',
              borderColor: '#ff6384',
              data: [
                { x: 10, y: 20 },
                { x: 15, y: 10 },
                { x: 20, y: 25 },
                { x: 30, y: 35 }
              ]
            }
          ]
        }
      };
    },
    mounted() {
      this.renderChart()
    },
    methods: {
      renderChart() {
        const ctx = this.$refs.chartCanvas.getContext('2d')
        new ChartJS(ctx, {
          type: 'scatter',
          data: this.chartData,
          options: { responsive: true, maintainAspectRatio: false }
        })
      }
    }
  }
  </script>
  
  <style scoped>
  canvas {
    width: 700px !important;
    height: 400px !important;
  }
  </style>
  